<template>
  <div class="content">
    <section class="classify">
      <div class="classifyLine">
        <div class="line" v-for="(item,index) of data" :key="index" >
          <div class="classifyName" @click="selectName(index)">
            {{item.typeName}}
            <van-icon class="ico" name="arrow-down" />
          </div>
          <van-overlay :show="show" @click="closeOverlay">
            <div class="wrapper">
              <div class="pullDown">
                <div class="pullContent">
                  <div
                    class="pull"
                    v-for="(el,idx) of data[currentIndex].typeItem"
                    :key="idx"
                    :id="el.id"
                  >
                    {{el.name}}
                    <!-- <van-icon v-if="currentIndex == index && idx == currentId" name="success" /> -->
                  </div>
                </div>
              </div>
            </div>
          </van-overlay>
        </div>
      </div>
    </section>
    <section class="houseStateContent">
      <div class="houseContent">
        <div
          class="houseState"
          v-for="(item,index) of this.getRespone.hosuseState"
          :key="index"
          :style="{'background':item.bgColor,'color':item.color}"
          @click="$router.push({path: '/houseDetail'})"
        >
          <p>{{item.num}}</p>
          <p>{{item.project}}</p>
          <p>{{item.type}}</p>
        </div>
      </div>
    </section>
    <footerBottom :tabbars="this.getRespone.bossTabbars" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: this.getRespone.getClassifyName,
      show: false,
      currentIndex: 0,
      currentId: null
    };
  },
  methods: {
    selectName(id) {
      this.show = true;
      this.currentIndex = id;
    },
    closeOverlay(e) {
      this.show = false;
      var currentId = e.target.id;
      this.currentId = currentId;
      var typeName = "";
      this.data[this.currentIndex].typeItem.forEach((element, index) => {
        if (element.id == currentId) {
          typeName = element.name;
        }
      });
      this.data[this.currentIndex].typeName = typeName;
    }
  }
};
</script>

<style lang="scss" scoped>
.classify {
  @include sc(0.85rem, $fColor);
  .classifyLine {
    @include grid(3, 1);
    margin: 0.8rem 0.5rem;
    .classifyName {
      @include fr(center);
      .ico {
        margin-left: 0.3rem;
      }
    }
  }
  .wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    height: 100%;
  }
  .van-overlay {
    top: 2.5rem;
    z-index: 2 !important;
    background-color:rgba(0,0,0,.3);

  }
  .pullDown {
    @include wh(100%, auto);
    background-color: $fc;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
    .pullContent {
      text-align: left;
      padding: 0.3rem 2.5rem;
      border-top: 0.05rem solid $F5;
      border-bottom-left-radius: 1rem;
      border-bottom-right-radius: 1rem;
      .pull {
        margin: 0.3rem 0rem;
        @include fr(space-between);
      }
    }
  }
}
.houseStateContent {
  padding: 0rem 0.8rem;
  .houseContent {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 相当于 1fr 1fr 1fr */
    grid-template-rows: repeat(3, 1fr); /* fr单位可以将容器分为几等份 */
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
    grid-auto-flow: row;
    .houseState {
      @include fCol(center, flex-start);
      text-align: left;
      border-radius: 0.4rem;
      padding: 0.8rem;
      p:first-child {
        font-size: 0.8rem;
        font-weight: bold;
        margin-bottom: 0.2rem;
      }
      p {
        font-size: 0.7rem;
      }
      p:last-child {
        margin-top: 0.2rem;
      }
    }
  }
}
</style>